main {
  display: flex;
  flex-direction: column;
  
  > * {
    flex: 0 1;
  }

  #inch {
    width: 1in;
    height: 1in;
    pointer-events: none;
    position: absolute;
    z-index: -100;
  }

  form {
    label {
      display: block;
    }
  }
}


